html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.boton {
  margin-bottom: 2rem;
  background-color: #0074D9;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0059a6;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    margin-bottom: 0rem;
  }
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 12rem;
  padding: 1.5rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
  text-align: center;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.modal {
  display: fixed;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
}
.modal .formulario {
  background-color: #4B5563;
  max-width: 50rem;
  background-color: #FFFFFF;
  padding: 0 0 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 800ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario .campo {
  text-transform: uppercase;
  display: flex;
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem 3rem 0rem 1rem;
}
.modal .formulario .campo label {
  font-size: 1.5rem;
  text-align: start;
  flex: 0 0 30rem;
  padding: 0.5rem;
}
.modal .formulario .campo .subtitulo {
  font-weight: 700;
  text-align: start;
  margin-left: 3rem;
  flex: 0 0 12rem;
}
.modal .formulario select {
  flex: 1;
  text-align: center;
  margin-right: 1rem;
  border-radius: 1rem;
  color: #4B5563;
  font-size: 1.5rem;
  height: 3.5rem;
  border: none;
  text-transform: uppercase;
}
.modal .formulario .contenedor-titulo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .formulario .imagen-estado {
  background-size: cover;
  background-position: center center;
  width: 4rem;
  height: 4rem;
}
.modal .formulario .imagen-estado.camino {
  background-image: url(../img/en_camino.gif);
}
.modal .formulario .imagen-estado.completa {
  background-image: url(../img/complete.gif);
}
.modal .formulario .imagen-estado.pendiente {
  background-image: url(../img/incomplete.gif);
}
.modal .formulario legend {
  font-size: 2.5rem;
  text-align: center;
  font-weight: bolder;
  margin-bottom: 2rem;
  color: #0074D9;
  display: block;
  padding: 2rem 1rem 0 0;
  border-radius: 1rem 1rem 0 0;
}
.modal .formulario .opciones {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .modal .formulario .opciones {
    flex-direction: row;
  }
}
.modal .formulario .submit-nuevo-usuario,
.modal .formulario .submit-actualizar-usuario {
  background-color: #0074D9;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nuevo-usuario,
.modal .formulario .submit-actualizar-usuario {
    width: auto;
  }
}
.modal .formulario .submit-nuevo-usuario:hover,
.modal .formulario .submit-actualizar-usuario:hover {
  background-color: #0059a6;
  cursor: pointer;
}
.modal .formulario .opciones-unica {
  display: flex;
  margin: 2rem;
  justify-content: center;
}
.modal .formulario .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.contenedor-opciones {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenedor-opciones {
    justify-content: space-between;
    flex-direction: row;
  }
}
.contenedor-opciones .buscador {
  display: flex;
  justify-content: end;
  justify-items: center;
  height: 4rem;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-opciones .buscador {
    width: 35rem;
  }
}
.contenedor-opciones .buscador select {
  flex: 1;
  text-align: center;
  margin-right: 1rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .contenedor-opciones .buscador select {
    flex: 1;
  }
}
.contenedor-opciones .buscador input[type=submit] {
  background-color: #0074D9;
  border: none;
  color: #FFFFFF;
  padding: 2rem;
  height: 4rem;
  text-align: center;
  line-height: 0px;
}

.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    min-height: 100vh;
    max-height: 3000vh;
  }
}
.dashboard .principal {
  flex: 1;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}

.sidebar {
  background-color: #4772e7;
  padding: 2rem;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
  font-size: 3rem;
}
.sidebar .contenedor-imagen {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sidebar .contenedor-imagen .imagen {
  display: flex;
  margin-right: 0.5rem;
  justify-content: center;
  height: 5rem;
  width: 5rem;
  background-image: url(../img/ups.png);
  background-size: cover;
  background-position: center center;
}
.sidebar .sidebar-nav {
  margin-top: 3rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 1.5rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #e6e6e6;
  color: #000;
  border-radius: 0.5rem;
}

.barra {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  margin: 0;
}
.barra p span {
  font-weight: 700;
