.listado-solicitudes {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-solicitudes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-solicitudes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-solicitudes .card {
  background-color: white;
  min-height: 15rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  line-height: 2rem;
  border-radius: 0.5rem;
  transition: all 0.5s;
}
.listado-solicitudes .card:hover {
  transform: scale(1.07);
  cursor: pointer;
}
.listado-solicitudes .card li {
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  position: relative;
}
.listado-solicitudes .card li .titulo {
  width: 100%;
  display: block;
  text-align: center;
  text-transform: uppercase;
  color: #0074D9;
  position: absolute;
  top: 0;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1rem 2rem 1.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
}
.listado-solicitudes .card li .titulo:hover {
  cursor: pointer;
}
.listado-solicitudes .card li .imagen-base {
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 190%;
  left: 43%;
  width: 8rem;
  height: 8rem;
}
@media (min-width: 768px) {
  .listado-solicitudes .card li .imagen-base {
    left: 39%;
  }
}
.listado-solicitudes .card li .imagen-base.camino {
