@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet{
        display: flex;
        min-height: 100vh;
        max-height: 3000vh;
    }

    .principal{
        flex: 1;
    }
    .contenido{
        padding: 2rem;
        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
        }
    }
}